<template>
  <div class="filter-container">
    <div class="title d-inline-block mr-3">
      <span class="theme">{{ theme }}</span>
      <span  class="subTheme float-right mr-4">{{ subTheme }}</span>
    </div>
    <div
      class="filter-content d-inline-block"
      :class="{
        hasBottom: isHas,
      }"
    >
      <span @mouseover="mouseover(i)" @mouseleave="mouseleave" ref="items" class="filter-item" v-for="(item,i) in filterItems" :key="item">
        {{ item }} <i class="el-icon-caret-bottom"></i>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: ["theme", "subTheme", "filterItems", "isHas","overMethod",'leaveMethod'],
  // overMethod Function hover时调用某个方法
  methods : {
    mouseover (i) {
      
      var l = this.$refs.items[i].offsetLeft;
      var t = this.$refs.items[i].offsetTop;
      this.$emit("lt",{l,t});
      this.overMethod && this.overMethod();
    },
    mouseleave() {
      this.leaveMethod && this.leaveMethod();
    }
  }
};
</script>

<style lang="less" scoped>
.filter-container {
  .title {
    height: 22px;
    line-height: 22px;
    width: 160px;
    vertical-align: top;
    .subTheme {
      border-radius: 100px;
      padding: 0 10px;
      font-size: 14px;
      background: linear-gradient(to bottom right, #ffd000, #ffbd00);
      color: #222222;
    }
  }
  .filter-content {
    &.hasBottom {
      padding-bottom: 10px;
      border-bottom: 1px solid #e5e5e5;
    }

    width: 75%;
    .filter-item {
      display: inline-block;
      min-width: 120px;
      padding: 0 10px;
      color: #666;
      line-height: 28px;
      font-size: 14px;
      cursor: pointer;
    }
  }
}
</style>