<template>
  <div @mouseover="mouseover" @mouseleave="mouseleave" class="over-container position-absolute">
    <h3>{{ popInfo.type }}</h3>
    <ul class="clearfix">
      <li v-for="item in popInfo.subTheme" :key="item">
        <a href="#">{{ item }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    popInfo: Object,
  },
  methods : {
    mouseover () {
      this.$emit('over')
    },
    mouseleave() {
      this.$emit('leave')
    }
  }
};
</script>

<style lang="less" scoped>
.over-container {
  z-index: 100;
  padding: 19px 15px;
  padding-bottom: 9px;
  background: #fff;
  border-radius: 4px;
  max-width: 510px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.15);
  &::before {
      content: '';
      position: absolute;
      left : 27px; 
      width: 8px;
      height: 8px;
      background: #fff;
      top : 0;
      transform: translateY(-50%) rotate(45deg);
      border-left: 1px solid #e5e5e5;
      border-top: 1px solid #e5e5e5;
  }
  h3 {
    color: #ccc;
    font-size: 16px;
    margin-bottom: 11px;
  }
  ul {
    width: 100%;
    li {
      float: left;
      min-width: 120px;
      a {
        margin-bottom: 5px;
        margin-left: -5px;
        font-size: 12px;
        line-height: 1.5;
        padding: 0 10px;
        color: #666;
        display: inline-block;
      }
    }
  }
}
</style>