  </div>
</template>

<script>
import GueItem from "./GueItem";
export default {
  components: {
    GueItem,
  },
  props : {
      gueItems : {
          type : Array,
          require : true
      },
      title : {
          type : String,
          default : '猜你喜欢'
      }
  }
};
