<template>
  <div class="w-100">
    <div class="s-container mx-auto">
      <Breadcrumb :bcs="bcs" />
      <div class="row clearfix w-100">
        <div class="s-left col">
          <div class="filter-box position-relative bg-white">
            <FilterBox
              @lt="offset = $event"
              :isHas="true"
              theme="分类"
              subTheme="全部"
              :filterItems="filter1"
              :overMethod="overMethod"
              :leaveMethod="leaveMethod"
            />
            <FilterBox
              @lt="offset = $event"
              :style="{
                'padding-top': '10px',
              }"
              theme="区域"
              subTheme="全部"
              :filterItems="filter2"
              :overMethod="overMethod"
              :leaveMethod="leaveMethod"
            />
            <PopOver
              @over="over"
              @leave="leave"
              v-show="isShow"
              :style="{
                left: offset.l + 'px',
                top: offset.t + 28 + 'px',
              }"
              :popInfo="subTheme"
            />
            <!-- 不是都能够指导某个中间去的 但是也可以调 -->
          </div>
          <div class="filter-region bg-white">
            <SortNav :sortList="navs" />
            <Good v-for="(good, i) in goodList" :key="i" :good="good" />
          </div>
        </div>

        <div class="s-right float-right">
          <div class="map-wrapper"></div>
          <GueLike
            v-if="rightMenuData.length > 0"
            :gueItems="rightMenuData"
          ></GueLike>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FilterBox from "@/components/FilterBox";
import SortNav from "@/components/SortNav";
import Good from "@/components/Good";
import GueLike from "@/components/GueLike";
import Breadcrumb from "@/components/Breadcrumb";
import PopOver from "@/components/PopOver";
export default {
  components: {
    FilterBox,
    SortNav,
    Good,
    GueLike,
    Breadcrumb,
    PopOver,
  },
  data() {
    return {
      isShow: false,
      isOver: false,
      bcs: [
        { title: "汕头美团", url: "/" },
        { title: "汕头美食", url: "/" },
        { title: "汕头生日蛋糕", url: "/" },
      ],
      filter1: ["美食", "休闲娱乐", "生活服务", "时尚购"],
      filter2: [
        "推荐商圈",
        "金平区",
        "濠江区",
        "龙湖区",
        "潮阳区",
        "潮南区",
        "澄海区",
        "南澳县",
      ],
      navs: [
        { name: "智能排序", func() {} },
        {
          name: "价格排序",
          func() {},
          ico: "el-icon-sort",
        },
        {
          name: "人气最高",
          func() {},
        },
        {
          name: "评价最高",
          func() {},
        },
      ],
      goodList: [],
      rightMenuData: [],
      subTheme: {},
      offset: { l: -1, t: -1 },
    };
  },
  async created() {
    var res = await this.$api.fetchGoodList();
    //   没有特别多跨组件要使用的数据 ，没必要使用vuex
    // 跨组件使用可以用eventBus 或 vuex
    this.goodList = res.data;
    var res1 = await this.$api.fetchRightMenu();
    this.rightMenuData = res1.data;

    var res2 = await this.$api.fetchSubTheme();
    this.subTheme = res2.data.item;
  },
  methods: {
    overMethod() {
      this.isShow = true; // 使用$listener也是一样的，其实都是组件通信的方式，不一定要全都用到
      // 我自己比较喜欢这种 我就用这种了
    },
    leaveMethod() {
      setTimeout(() => {
        if (!this.isOver) {
          this.isShow = false;
        }
      }, 0);
    },
    over() {
      this.isOver = true;
      // 这个出现的细节处理的不好 还有bug 暂时不想了
    },
    leave() {
      this.isOver = false;
      this.isShow = false;
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/css/common/var.less";
.s-container {
  width: 1190px;

  .filter-box {
    padding: 15px 20px;
    text-align: left;
    background: #fff;
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 10%);
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    margin-bottom: 10px;
  }
  .filter-region {
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    div {
      padding: 11px 20px;
    }
  }
}
.s-right {
  .map-wrapper {
    width: 230px;
    height: 222px;
    background-color: #fff;
    border-radius: 4px;
  }
  .gue-like-container {
    background-color: #fff;
    border-radius: 4px;
    width: 230px;
    padding: 16px 20px 0;
    margin-top: 10px;
  }
}
</style>